import React from "react";
import { useDispatch, useSelector } from "react-redux";

import { isAuth } from "../../../assets/helper/auth";
import {
  commonGetRequest,
  commonPostRequest,
  setSnackBar,
} from "../../../redux/features/Auth";
import {
  sportsListUrl,
  generateSnackData,
  generateISODate,
  getSlotsAvailUrl,
} from "../../../assets/helper/constants";

import Form from "./Form";

export default function Slots() {
  const dispatch = useDispatch();
  const { sports_list, slots_bulk_list } = useSelector((state) => state.auth);
  const [complexId] = React.useState(isAuth().vendor_complex_id);

  console.log("slots_bulk_list :>> ", slots_bulk_list);

  const loadInitialValues = () => {
    dispatch(
      commonGetRequest({
        url: sportsListUrl,
        key: "sports_list",
      })
    );
  };

  React.useEffect(() => {
    loadInitialValues();
  }, []);

  const changeState = (data) => {
    if (Object.keys(data).length === 6) {
      let formData = {
        ...data,
        from_date: generateISODate(data.from_date),
        to_date: generateISODate(data.to_date),
      };
      console.log("formData :>> ", formData);
      dispatch(
        commonPostRequest({
          url: getSlotsAvailUrl,
          key: "slots_bulk_list",
          formData: formData,
        })
      );
    } else {
      dispatch(
        setSnackBar(
          generateSnackData(true, "Please select all fields.", "error")
        )
      );
    }
  };

  return (
    <>
      <Form
        sportData={sports_list}
        vendor_complex_id={complexId}
        changeState={(data) => changeState(data)}
      />
    </>
  );
}
