import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import Server from "../../Server";
import {
  generateSnackData,
  loginUrl,
  getSlotsAvailUrl,
  members_list,
  step1UpdateUrl,
  step1AddUrl,
  step2UpdateUrl,
  step2AddUrl,
  step3UpdateUrl,
  step3AddUrl,
  bookingAdd,
  bookingPayment,
} from "../../assets/helper/constants";
import { authenticate } from "../../assets/helper/auth";

export const loginSubmitThunk = createAsyncThunk(
  "auth/loginSubmitThunk",
  async (payload, { rejectWithValue, dispatch }) => {
    try {
      let data = await Server.post(loginUrl, payload);
      return data;
    } catch (error) {
      const resp =
        error.response && error.response.data.message
          ? error.response.data.message
          : "Couldn't connect to our servers.";
      dispatch(setSnackBar(generateSnackData(true, resp, "error")));
      rejectWithValue(error);
    }
  }
);

export const step1AddOrUpdate = createAsyncThunk(
  "auth/step1AddOrUpdate",
  async (payload, { rejectWithValue, dispatch }) => {
    try {
      let data = await Server.post(
        payload.add === 1 ? step1AddUrl : step1UpdateUrl,
        payload.data
      );
      return data;
    } catch (error) {
      const resp = error.response
        ? error.response.data.message
        : "Couldn't connect to our servers.";
      dispatch(setSnackBar(generateSnackData(true, resp, "error")));
      rejectWithValue(error);
    }
  }
);

